import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

export const fetchPizzasDefault = createAsyncThunk('pizza/fetchPizzasDefaultStatus', async (params, thunkAPI) => {
    const { categoryID, sort, search, orderFlag} = params
    const {data} = await axios.get(categoryID === 0 
        ? `https://668ff050c0a7969efd9a3ea7.mockapi.io/items?sortBy=${sort}${`&order=${orderFlag}`}${search}` 
        : `https://668ff050c0a7969efd9a3ea7.mockapi.io/items?category=${categoryID}&sortBy=${sort}${`&order=${orderFlag}`}${search}`)

    return data
    }
)

export const fetchPizzas = createAsyncThunk('pizza/fetchPizzasStatus', async (params, thunkAPI) => {
    const { categoryID, pageCount, limitItemsOnPage, sort, search, orderFlag} = params
    const { data } = await axios.get(categoryID === 0 
        ? `https://668ff050c0a7969efd9a3ea7.mockapi.io/items?page=${pageCount}&limit=${limitItemsOnPage}&sortBy=${sort}${`&order=${orderFlag}`}${search}` 
        : `https://668ff050c0a7969efd9a3ea7.mockapi.io/items?category=${categoryID}&page=${pageCount}&limit=${limitItemsOnPage}&sortBy=${sort}${`&order=${orderFlag}`}${search}`)

    return data
    }
)

const initialState = {
    pizzasOnBackEndDefault: [],
    pizzasOnBackEnd: [],
    status: 'loading'    // loading(загрузка) | success(успех) | error(ошибка)
}

const pizzaSlice = createSlice({
    name: 'pizza',
    initialState,
    reducers: {
        setPizzasOnBackEnd(state, action) {
            state.pizzasOnBackEnd = action.payload
        }
    },
    extraReducers: (builder) => {
        builder.addCase(fetchPizzasDefault.fulfilled, (state, action) => {
            state.pizzasOnBackEndDefault = action.payload
        }).addCase(fetchPizzas.pending, (state) => {
            state.status = 'loading'
            state.pizzasOnBackEnd = []
          })
        .addCase(fetchPizzas.fulfilled, (state, action) => {
            state.pizzasOnBackEnd = action.payload
            state.status = 'success'
        })
        .addCase(fetchPizzas.rejected, (state) => {
            state.status = 'error'
            state.pizzasOnBackEnd = []
        })
    }
})

export const pizzaSelector = (state) => state.pizzaReducer

export const { setPizzasOnBackEnd } = pizzaSlice.actions

export default pizzaSlice.reducer