import { useState } from "react"
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux"
import { addItem, itemPizzaSelector } from "../../redux/slices/cartSlice"

function PizzaBlock(props) {
    const { title, price, imageUrl, sizes, types, id } = props

    const typeNames = ['Тонкое', 'Традиционное']

    const [sizeActive, setSizeActive] = useState(0)
    const [typesActive, setTypezActive] = useState(types[0])

    const dispatch = useDispatch()

    const itemPizza = useSelector(itemPizzaSelector(id))
    const count = itemPizza ? itemPizza.count : 0

    const onClickAdd = () => {
        const item = {
            id,
            title,
            price,
            imageUrl,
            types: typeNames[typesActive],
            sizes: sizes[sizeActive]
        }
        dispatch(addItem(item))
    }

    {/* Создаем путь для каждой пиццы. В конце пути в шаблонной строке */}
{/* указываем id, который находится в параметрах объекта этой пиццы  */}

    return (
        <div className="pizza-block-wrapper">
            <div className="pizza-block">
                <Link to={`/fullPizza/${id}`}>        {/* Создаем путь для каждой отдельной пиццы. В конце пути в шаблонной строке */}
                                                      {/* указываем id, который находится в параметрах объекта этой пиццы  */}
                                                      
                    <img className="pizza-block__image" src={imageUrl} alt="Pizza" />
                    
                    <h4 className="pizza-block__title">{title}</h4>

                    <div className="pizza-block__selector">
                        <ul>
                            {
                            types.map((typeID) => <li className={typesActive === typeID ? 'active' : ''} onClick={() => setTypezActive(typeID)} key={typeID}>{typeNames[typeID]}</li>)
                            }
                        </ul>

                        <ul>
                            {
                            sizes.map((size, i) => <li className={sizeActive === i ? 'active' : ''} onClick={() => setSizeActive(i)} key={i}>{size} см.</li>)
                            }
                        </ul>
                    </div>
                </Link>

                <div className="pizza-block__bottom">
                    <div className="pizza-block__price">от {price} ₽</div>
                    
                    <button onClick={onClickAdd} type="button"  className="button button--outline button--add">
                        <svg className='button_svg' width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z" fill="white"/>
                        </svg>

                        <span>Добавить</span>
                        {count > 0 && <i>{count}</i>}
                    </button>
                </div>
            </div> 
        </div>
    )
}

export default PizzaBlock