import React from "react";
import axios from "axios";
import qs from "qs"; // Библиотека qs расшифровывается как QueryString

import Categories from "../components/Categories"
import Sort from "../components/Sort"
import PizzaBlock from '../components/PizzaBlock/PizzaBlock'
import Skeleton from '../components/PizzaBlock/Skeleton';
import Pagination from "../components/Pagination";

import { useNavigate } from "react-router";
import { SearchContext } from "../App.jsx"; // Импортируем контекст 
import { useDispatch, useSelector } from "react-redux";
import filterSlice, { setPage, setFilters, filterSelector } from "../redux/slices/filterSlice";
import { fetchPizzas, fetchPizzasDefault, pizzaSelector } from "../redux/slices/pizzaSlice";
import { Link } from "react-router-dom";


// Копия массива из файла pizzas.json на удаленном сервере, созданном с помощью mockapi.io :  https://668ff050c0a7969efd9a3ea7.mockapi.io/items

// ↓
// ↑




const Home = () => {
  const navigate = useNavigate()
  const isSearchStrokeRef = React.useRef(false)
  const isMounted = React.useRef(false) // Mounted переводится как смонтирован, т.е. если что-то уже отрендерилось(смонтировалось) первый раз
  const { searchValue } = React.useContext(SearchContext) // Используем контекст(useContext)
  
  const { categoryID, popupSort, pageInitObj } = useSelector(filterSelector)
  const { pizzasOnBackEndDefault, pizzasOnBackEnd, status } = useSelector(pizzaSelector)

  const dispatch = useDispatch()

  let noGrid = status === 'error' ? 'no-grid' : ''
  let orderFlag = popupSort.rotateSortSVG === 'rotate_180' ? 'desc' : 'asc'  // asc и desc меняет сортировку на по возрастанию и на по убыванию. Так это работает на сервере mockapi.io
                                                                             // т.е. в ссылке в fetch запросе получается или order=desc(по убыванию) или order=asc(по возрастанию)
                                                                                  
  const search = searchValue ? `&title=${searchValue}` : ''
 
  function fetchData() {
    dispatch(fetchPizzasDefault({
      categoryID,
      sort: popupSort.sort,
      search,
      orderFlag
    }))

    if (!Object.keys(qs.parse(window.location.search.substring(1))).length === 0) { // Проверяем наличие в поисковой строке данных
      dispatch(setFilters({...qs.parse(window.location.search.substring(1))}))
    }
    dispatch(setPage({...pageInitObj, allItemsFromServer: pizzasOnBackEndDefault.length}))

    dispatch(fetchPizzas({
          categoryID,
          pageCount: pageInitObj.pageCount,
          limitItemsOnPage: pageInitObj.limitItemsOnPage,
          sort: popupSort.sort,
          search,
          orderFlag
        }))
  }

//----------------------------------- Создание поисковой строки START -----------------------------------------
  React.useEffect(() => {
    if(isMounted.current) {
      const queryString = qs.stringify({
        category: categoryID,
        page: pageInitObj.pageCount,
        sortBy: popupSort.sort,
      })
    
      navigate(`?${queryString}`)
    }
    isMounted.current = true
    },[categoryID, pageInitObj.pageCount, popupSort.sort]
  )
//----------------------------------- Создание поисковой строки END -------------------------------------------


//----------------------------------- Создание запроса на сервер из поисковой строки START --------------------------------------------
  React.useEffect(() => {
    if(window.location.search) {
      const params = qs.parse(window.location.search.substring(1))

      dispatch(setFilters({...params}))
      isSearchStrokeRef.current = true
    }
  },[categoryID, pageInitObj.pageCount, popupSort.sort, orderFlag])
//---------------------------------- Создание запроса на сервер из поисковой строки END ------------------------------------------------


  //----------------------------------------------------- Запрос на сервер START --------------------------------------------------
  React.useEffect(() => {
    fetchData()
    if(isSearchStrokeRef.current) {
      fetchData()
    }
    window.scrollTo(0, 0) // При первом рендере страницы этот код из fetch сначала запросит все данные по пиццам,
                            // а затем сделает скролл вверх на координаты x(икс) и y(игрик) равные (0, 0)
    },[categoryID, popupSort, searchValue, search, pageInitObj.pageCount, pizzasOnBackEndDefault.length])
  //---------------------------------------------------- Запрос на сервер END -------------------------------------------------------

  const skeletons = [...new Array(13)].map((_, i) => <Skeleton key={i} />)
  return (
    <>
      <div className="content__top">
          <Categories />

          <Sort />
      </div>

      <h2 className="content__title">Все пиццы</h2>

      <div className={`content__items ${noGrid}`}>
        {status === 'loading'
          ? skeletons 
          : (status === 'error'
                        ? <p className="not-found">Пицца с таким названием не найдена, попробуйте поискать в другой категории или изменить запрос в поисковой строке</p> 
                        : pizzasOnBackEnd.map((pizza, i) => <PizzaBlock {...pizza} key={pizza.id} />)
            )
        }
      </div>

      {status === 'error' ? '' : <Pagination />}
    </>
  )
}

export default Home